#timeline-experience {
    position: relative;
  }
  
  #timeline-experience:before {
    content: '';
    position: absolute;
    top: 0;
    bottom: 23px;
    left: 50%;
    height: 99.5%;
    width: 4px;
    opacity: 0.3;
    background: #2c3e50;
  }
  
  #timeline-experience {
    position: relative;
  }
  
  #timeline-experience::before {
    content: '';
    position: absolute;
    top: 0;
    bottom: 23px;
    left: 50%;
    height: 99.5%;
    width: 4px;
    opacity: 0.3;
    background: #2c3e50;
  }
  
  .timeline-block {
    position: relative;
  }
  
  .timeline-block::after {
    clear: both;
    content: "";
    display: table;
  }
  
  .timeline-block:first-child {
    margin-top: 0;
  }
  
  .timeline-block:last-child {
    margin-bottom: 0;
  }
  
  .timeline-block .card-content {
    padding: 20px;
  }
  
  .timeline-block .card-content p {
    margin-top: 13px;
    text-align: left;
  }
  
  .timeline-block .card-content .modal-dot {
    outline: 0;
    font-size: 20px;
  }
  
  .timeline-block .card-content .modal-dot:hover,
  .timeline-block .card-content .modal-dot:focus {
    color: #12579b;
  }
  
  .timeline-block .timeline-dot {
    background: #2c3e50;
    width: 50px;
    height: 50px;
    border-radius: 100%;
    -webkit-box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
            box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
    z-index: 2;
    color: #fff;
    text-align: center;
    position: absolute;
    top: 5px;
    right: 2%;
  }
  
  .timeline-block .timeline-dot i {
    text-align: center;
    font-size: 24px;
    margin-top: 26%;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.29);
  }
  
  .timeline-block .timeline-dot h6 {
    padding: 0;
    font-size: 30px;
    font-weight: bold;
    margin-top: 8px;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.29);
  }
  
  .timeline-block .timeline-content {
    position: relative;
    background: #fff;
    margin-right: 0;
    z-index: 1;
    margin-bottom: 5px;
    border-left: 5px solid #1976D2;
  }
  
  .timeline-block .timeline-content::before {
    content: '';
    width: 58px;
    height: 4px;
    position: absolute;
    top: 26px;
    left: 100%;
    z-index: -2;
    opacity: 0.4;
    background: #2c3e50;
  }
  
  .timeline-block .timeline-content::after {
    clear: both;
    content: "";
    display: table;
  }
  
  .timeline-block .timeline-content .timeline-title {
    max-width: 84%;
    font-weight: normal;
  }
  
  .timeline-block .timeline-content .timeline-info {}
  
  .timeline-block .timeline-content .timeline-info h6 {
    line-height: 12px;
  }
  
  .timeline-block .timeline-content .timeline-info h6 small {
    color: inherit;
  }
  
  @media screen and (max-width:768px) {
    .timeline-block .timeline-content:before {
      display: none;
    }
  }
  
  @media only screen and (min-width:768px) {
    #timeline-experience::before {
      left: 50%;
      margin-left: -2px;
      height: 77.1%;
      opacity: 0.4;
      background: #2c3e50;
    }
    #timeline-experience::before {
      left: 50%;
      margin-left: -2px;
      height: 69.1%;
      opacity: 0.4;
      background: #2c3e50;
    }
    .timeline-block {
      margin: -5em 0;
    }
    .timeline-block .card {
      margin: 0;
    }
    .timeline-block .timeline-dot {
      width: 50px;
      height: 50px;
      left: 50%;
      top: 0;
      z-index: 2;
      margin-left: -24px;
      -webkit-transform: translateZ(0);
      -webkit-backface-visibility: hidden;
    }
    .timeline-block .timeline-content {
      width: 44%;
    }
    .timeline-block .timeline-content .timeline-title {
      max-width: 100%;
    }
    .timeline-block:nth-child(even) .timeline-content {
      float: right;
    }
    .timeline-block:nth-child(even) .timeline-content::before {
      top: 25px;
      left: auto;
      right: 101.2%;
      z-index: -2;
    }
  }


